___CSS_LOADER_EXPORT___.push([module.id, "\n.combat-cell.many[data-v-11ed2e34] {\n  display: grid;\n  grid-template-columns: 50% 50%;\n  grid-template-rows: 50% 50%;\n}\n.combat-cell.many > .combat-unit[data-v-11ed2e34] {\n  width: 0.75rem;\n  height: 0.75rem;\n  font-size: 0.5rem;\n}\n.combat-empty-cell[data-v-11ed2e34] {\n  width: 100%;\n  height: 100%;\n}\ntable[data-v-11ed2e34] {\n  border-collapse: collapse;\n  border-spacing: 0;\n  padding: 0;\n  margin: 1rem;\n  table-layout: fixed;\n  width: 1px; /* To prevent expansion of cells, condition for table-layout to work */\n}\ntd[data-v-11ed2e34] {\n  border: 1px solid gray;\n  padding: 0;\n  margin: 0;\n}\ntd[data-v-11ed2e34] {\n  height: 1.5rem;\n  width: 1.5rem;\n}\n.unit[data-v-11ed2e34] {\n  width: calc(1.5rem - 1px); /* Leave 1px space for td border */\n  height: calc(1.5rem - 1px);\n}\n", "",{"version":3,"sources":["webpack://./src/components/CombatGrid.vue"],"names":[],"mappings":";AA8FA;EACE,aAAa;EACb,8BAA8B;EAC9B,2BAA2B;AAC7B;AACA;EACE,cAAc;EACd,eAAe;EACf,iBAAiB;AACnB;AACA;EACE,WAAW;EACX,YAAY;AACd;AACA;EACE,yBAAyB;EACzB,iBAAiB;EACjB,UAAU;EACV,YAAY;EACZ,mBAAmB;EACnB,UAAU,EAAE,sEAAsE;AACpF;AACA;EACE,sBAAsB;EACtB,UAAU;EACV,SAAS;AACX;AACA;EACE,cAAc;EACd,aAAa;AACf;AACA;EACE,yBAAyB,EAAE,kCAAkC;EAC7D,0BAA0B;AAC5B","sourcesContent":["<template>\n  <table class=\"combat-grid\">\n    <tr v-for=\"(row, y) in grid\" :key=\"y\">\n      <td\n        v-for=\"(cell, x) in row\"\n        :key=\"x\"\n        :class=\"{ 'combat-cell': true, many: cell.units.length > 1 }\"\n        @drop=\"onDropOnCell($event, { x, y })\"\n        @dragover.prevent\n        @dragenter.prevent\n      >\n        <combat-enemy\n          v-for=\"unit in cell.units.filter(u => u.unitType === 'enemy')\"\n          :key=\"unit.id\"\n          class=\"unit\"\n          draggable=\"true\"\n          :unit=\"unit\"\n          @dragstart=\"onUnitDragStart($event, unit, { x, y })\"\n        />\n        <combat-character\n          v-for=\"unit in cell.units.filter(u => u.unitType === 'character')\"\n          :key=\"unit.id\"\n          class=\"unit\"\n          draggable=\"true\"\n          :unit=\"unit\"\n          @dragstart=\"onUnitDragStart($event, unit, { x, y })\"\n        />\n        <combat-ally\n          v-for=\"unit in cell.units.filter(u => u.unitType === 'ally')\"\n          :key=\"unit.id\"\n          class=\"unit\"\n          draggable=\"true\"\n          :unit=\"unit\"\n          @dragstart=\"onUnitDragStart($event, unit, { x, y })\"\n        />\n        <div v-if=\"!cell.units.length\" class=\"combat-empty-cell\" />\n      </td>\n    </tr>\n  </table>\n</template>\n\n<script>\nimport { mapState, mapActions, mapGetters } from 'vuex'\nimport CombatAlly from './CombatAlly.vue'\nimport CombatEnemy from './CombatEnemy.vue'\nimport CombatCharacter from './CombatCharacter.vue'\nimport { GRID_WIDTH, GRID_HEIGHT } from '../stores/combat.store'\n\nexport default {\n  name: 'CombatGrid',\n  components: {\n    CombatAlly,\n    CombatEnemy,\n    CombatCharacter,\n  },\n  computed: {\n    ...mapState({\n      units: state => state.combat.units,\n    }),\n    ...mapGetters({\n      getUnitById: 'combat/getUnitById',\n    }),\n    grid() {\n      const grid = Array(GRID_WIDTH)\n        .fill(null)\n        .map(_y =>\n          Array(GRID_HEIGHT)\n            .fill(null)\n            .map(_x => ({ units: [] }))\n        )\n      this.units.forEach(unit => grid[unit.position.y][unit.position.x].units.push(unit))\n      return grid\n    },\n  },\n  methods: {\n    ...mapActions('combat', ['moveUnit']),\n    onUnitDragStart(event, unit, oldPosition) {\n      event.dataTransfer.dropEffect = 'move'\n      event.dataTransfer.effectAllowed = 'move'\n      event.dataTransfer.setData('dragUnit', JSON.stringify({ unit, oldPosition }))\n    },\n    onDropOnCell(event, newPosition) {\n      const { unit, oldPosition } = JSON.parse(event.dataTransfer.getData('dragUnit'))\n      const unitRef = this.getUnitById(unit.id)\n      if (!unitRef) {\n        throw new Error(`Could not find unit with id ${unit.id}`)\n      }\n      this.moveUnit({ unit: unitRef, oldPosition, newPosition })\n    },\n  },\n}\n</script>\n\n<style scoped>\n.combat-cell.many {\n  display: grid;\n  grid-template-columns: 50% 50%;\n  grid-template-rows: 50% 50%;\n}\n.combat-cell.many > .combat-unit {\n  width: 0.75rem;\n  height: 0.75rem;\n  font-size: 0.5rem;\n}\n.combat-empty-cell {\n  width: 100%;\n  height: 100%;\n}\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n  padding: 0;\n  margin: 1rem;\n  table-layout: fixed;\n  width: 1px; /* To prevent expansion of cells, condition for table-layout to work */\n}\ntd {\n  border: 1px solid gray;\n  padding: 0;\n  margin: 0;\n}\ntd {\n  height: 1.5rem;\n  width: 1.5rem;\n}\n.unit {\n  width: calc(1.5rem - 1px); /* Leave 1px space for td border */\n  height: calc(1.5rem - 1px);\n}\n</style>\n"],"sourceRoot":""}]);
